<template>
  <div>
    <!--  git -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" style="margin-bottom:20px" @click="showAddDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        :data="mCategoriesList"
        style="width: 100%"
        :columns="columns"
        :show-index="true"
        border
        :expand-type="false"
        :show-row-hover="false"
        :selection-type="false"
        v-loading="loading"
      >
        <!--  -->
        <template slot="catDeletedTmpl" slot-scope="scope">
          <div>
            <i
              class="el-icon-success"
              v-if="scope.row.cat_deleted===false"
              style="color:lightgreen;"
            ></i>
            <i class="el-icon-error" v-else style="color:red;"></i>
          </div>
        </template>
        <!--  -->
        <template slot="orderTmpl" slot-scope="scope">
          <div>
            <el-tag v-if="scope.row.cat_level === 0"></el-tag>
            <el-tag type="success" v-else-if="scope.row.cat_level === 1"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </div>
        </template>
        <!--  -->
        <template slot="optTmpl">
          <div>
            <el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete"></el-button>
          </div>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[3, 4, 5, 7]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="mTotal"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addCateVisible" @close="resetForm">
      <el-form :model="cateForm" ref="cateFormRef" :rules="rules">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="cateForm.cat_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            v-model="selectedOption"
            :options="mParentsList"
            :props="cascadeProps"
            @change="handleChange"
            class="cascader"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addCateVisible = false"> </el-button>
        <el-button type="primary" @click="addCategory"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loading: false,
      queryInfo: {
        type: '',
        pagenum: 1,
        pagesize: 7
      },
      //   
      mCategoriesList: [],
      //   
      mTotal: 0,
      // 
      columns: [
        { label: '', prop: 'cat_name' },
        {
          label: '',
          type: 'template',
          template: 'catDeletedTmpl'
        },
        {
          label: '',
          type: 'template',
          template: 'orderTmpl'
        },
        {
          label: '',
          type: 'template',
          template: 'optTmpl'
        }
      ],
      // 
      addCateVisible: false,
      // 
      cateForm: {
        // 
        cat_name: '',
        // 1Id  0
        cat_pid: 0,
        // 012
        cat_level: 0
      },
      // 
      rules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 6, message: ' 2  6 ', trigger: 'blur' }
        ]
      },
      // 
      cascadeProps: {
        // 
        value: 'cat_id',
        // 
        label: 'cat_name',
        // 
        children: 'children',
        //  
        expandTrigger: 'hover',
        // 
        checkStrictly: true
      },
      // 
      mParentsList: [],
      // 
      selectedOption: []
    }
  },
  methods: {
    async fetchProductList() {
      this.loading = true

      const { meta, data } = (await this.$http.get('categories', {
        params: this.queryInfo
      })).data

      this.loading = false
      if (meta.status === 200) {
        this.mCategoriesList = data.result
        this.mTotal = data.total
      } else {
        this.$message.error(meta.msg)
      }
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.fetchProductList()
    },
    handleCurrentChange(curSize) {
      this.queryInfo.pagenum = curSize
      this.fetchProductList()
    },
    // 
    async fetchParentsList() {
      const { meta, data } = (await this.$http.get('categories', {
        params: { type: 2 }
      })).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mParentsList = data
      console.log(this.mParentsList)
    },
    // 
    showAddDialog() {
      this.fetchParentsList()
      this.addCateVisible = true
    },
    // 
    // cat_pid 1Id  0
    // cat_level 012
    handleChange() {
      // 
      // 
      if (this.selectedOption.length > 0) {
        const index = this.selectedOption.length - 1
        this.cateForm.cat_pid = this.selectedOption[index]
        this.cateForm.cat_level = this.selectedOption.length
      } else {
        this.cateForm.cat_pid = 0
        this.cateForm.cat_level = 0
      }
    },
    // 
    addCategory() {
      this.$refs.cateFormRef.validate(async valid => {
        if (!valid) return
        const { meta } = (await this.$http.post(
          'categories',
          this.cateForm
        )).data
        console.log(meta)

        if (meta.status !== 201) return this.$message.error(meta.msg)
        this.$message.success('')
        this.fetchProductList()
        this.addCateVisible = false
      })
    },
    // 
    resetForm() {
      this.$refs.cateFormRef.resetFields()
      this.cateForm.cat_pid = 0
      this.cateForm.cat_level = 0
      this.selectedOption = []
    }
  },
  created() {
    this.fetchProductList()
  }
}
</script>

<style scoped>
.cascader {
  width: 100%;
}
</style>
