<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
      <!--   stepIndex activenumber0,ascii-->
      <el-steps :space="200" :active="stepIndex-0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--   -->
      <!-- label-position left  right  label-width -->
      <el-form :model="form" :rules="rules" ref="formRef" label-width="100px" label-position="top">
        <!--  -->
        <!-- v-model name string -->
        <!-- :before-leave -->
        <el-tabs
          :tab-position="'left'"
          v-model="stepIndex"
          :before-leave="beforeLeave"
          @tab-click="tabClick"
        >
          <!-- name 0 name -->
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="form.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="form.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="form.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="form.goods_number" type="number"></el-input>
            </el-form-item>
            <!-- ',' -->
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="form.goods_cat"
                :options="mCategoriesList"
                :props="cascaderProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
            <el-button @click="nextTab" class="goNext"></el-button>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!--  -->
            <el-form-item :label="item.attr_name" v-for="item in dynamicData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="subItem" v-for="(subItem,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-button @click="nextTab"  class="goNext"></el-button>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in staticData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>

            <el-button @click="nextTab" class="goNext"></el-button>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              class="upload-demo"
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="picReqHeader"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
            <el-button @click="nextTab"  class="goNext"></el-button>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="form.goods_introduce"></quill-editor>
            <el-button @click="addProduct" class="btn-addProduct" type="primary"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="30%">
      <img :src="previewPath" alt width="100%" />
    </el-dialog>
  </div>
</template>

<script>
// lodash

import _ from 'lodash'
export default {
  data() {
    return {
      // 
      stepIndex: '0',
      //   
      form: {
        goods_name: '',
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        // 
        goods_introduce: '',
        // 
        pics: [],
        attrs: []
      },
      // 
      rules: {
        // 
        goods_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 18, message: ' 2  18 ', trigger: 'blur' }
        ],
        // 
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //   
      mCategoriesList: [],
      //   
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      //   
      selectedOption: [],
      // 
      dynamicData: [],
      // 
      staticData: [],
      // 
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 
      picReqHeader: {
        Authorization: window.sessionStorage.getItem('access_token')
      },
      // 
      previewPath: '',
      // 
      previewVisible: false
    }
  },
  methods: {
    async fetchCateList() {
      const { meta, data } = (await this.$http.get('categories')).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mCategoriesList = data
      // console.log(this.mCategoriesList)
    },
    // 
    handleChange() {
      if (this.form.goods_cat.length !== 3) {
        this.form.goods_cat = []
        this.$message.error('')
      }
    },
    // 
    beforeLeave(activeName, oldActiveName) {
      // console.log('' + activeName)
      // console.log('' + oldActiveName)
      if (oldActiveName === '0' && this.form.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // 
    async tabClick() {
      // console.log(typeof this.stepIndex) //string
      switch (this.stepIndex) {
        case '1':
          const { meta, data } = (await this.$http.get(
            `categories/${this.cateId}/attributes`,
            { params: { sel: 'many' } }
          )).data
          if (meta.status !== 200) return this.$message.error(meta.msg)
          this.dynamicData = data
          this.dynamicData.forEach(item => {
            item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          })
          // console.log(this.dynamicData)
          break
        case '2':
          const { meta: sMeta, data: sData } = (await this.$http.get(
            `categories/${this.cateId}/attributes`,
            { params: { sel: 'only' } }
          )).data
          if (sMeta.status !== 200) return this.$message.error(sMeta.msg)
          this.staticData = sData
          break
        default:
          break
      }
    },
    // 
    nextTab() {
      // stepIndexstring+1,
      var stepNum = this.stepIndex - 0 + 1
      // 
      if (stepNum > 4) stepNum = 0
      this.stepIndex = stepNum + ''
      this.tabClick()
      // console.log(typeof this.stepIndex)
      // console.log(this.stepIndex)
    },
    // 
    handlePreview(file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 
    handleRemove(file, fileList) {
      const index = this.form.pics.findIndex(
        item => item.pic === file.response.data.tmp_path
      )
      this.form.pics.splice(index, 1)
    },
    handleSuccess(response, file, fileList) {
      if (response.meta.status !== 200) return this.$message.error('')
      this.form.pics.push({
        pic: response.data.tmp_path
      })
      this.$message.success('')
    },
    // 
    addProduct() {
      this.$refs.formRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        // cat_id
        //  
        const copyForm = _.cloneDeep(this.form)
        copyForm.goods_cat = copyForm.goods_cat.join(',')

        // attr
        this.dynamicData.forEach(item => {
          let attrInfo = {}
          attrInfo.attr_id = item.attr_id
          attrInfo.attr_value = item.attr_vals
          copyForm.attrs.push(attrInfo)
        })

        this.staticData.forEach(item => {
          let attrInfo = {}
          attrInfo.attr_id = item.attr_id
          attrInfo.attr_value = item.attr_vals
          copyForm.attrs.push(attrInfo)
        })

        // 
        const { meta } = (await this.$http.post('goods', copyForm)).data
        console.log(meta)
        if (meta.status !== 201) return this.$message.error(meta.status)
        this.$message.success('')
        this.$router.push('/goods')
        // console.log(this.form)
        // console.log(copyForm)
      })
    }
  },
  created() {
    this.fetchCateList()
  },
  computed: {
    cateId() {
      if (this.form.goods_cat.length === 3) return this.form.goods_cat[2]
      return null
    }
  }
}
</script>

<style scoped>
.el-checkbox {
  margin-right: 0px !important;
}
.btn-addProduct {
  float: right;
  margin-top: 15px;
}
.goNext{
  margin-top:15px;
}
</style>
