<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col>
          <el-button
            type="primary"
            style="margin-bottom:20px"
          ></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="mRolesList"
        style="width: 100%"
        border
        v-loading="loading"
        row-key="id"
      >
        <!--  children -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <div>
              <el-row
                v-for="(item,i) in scope.row.children"
                :key="item.id"
                :class="[i===0?'borderd-top':'','borderd-bottom','dispaly-center ']"
              >
                <el-col :span="5">
                  <!-- iD  -->
                  <el-tag
                    closable
                    @close="removeTagById(scope.row,item.id)"
                  >{{item.authName}}</el-tag>
                  <i class="el-icon-arrow-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    v-for="(subItem,j) in item.children"
                    :key="subItem.id"
                    :class="[j!==0?'borderd-top':'','dispaly-center']"
                  >
                    <el-col :span="5">
                      <el-tag
                        type="danger"
                        closable
                        @close="removeTagById(scope.row,subItem.id)"
                      >{{subItem.authName}}</el-tag>
                      <i class="el-icon-arrow-right"></i>
                    </el-col>
                    <el-col :span="14">
                      <el-row
                        v-for="(grandItem,k) in subItem.children"
                        :key="grandItem.id"
                        :class="[k!==0?'borderd-top':'','dispaly-center']"
                      >
                        <el-tag
                          closable
                          @close="removeTagById(scope.row,grandItem.id)"
                          type="warning"
                        >{{grandItem.authName}}</el-tag>
                      </el-row>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </div>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column
          label=""
          prop="roleName"
        ></el-table-column>
        <el-table-column
          label=""
          prop="roleDesc"
        ></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              class="el-icon-edit"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              class="el-icon-delete"
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              class="el-icon-s-tools"
              @click="showPermissionDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="permissionDialogVisible"
      width="30%"
      @close="resetDefaultKeys"
    >
      <el-tree
        :data="mPermissionList"
        :props="treeProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-expanded-keys="defaultKeys"
        :default-checked-keys="defaultKeys"
        ref="treeRef"
      ></el-tree>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="permissionDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="allotsPermissions"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 
      loading: false,
      //  children
      mRolesList: [],
      // 
      mPermissionList: [],
      // 
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // ID
      defaultKeys: [],
      // - 
      permissionDialogVisible: false,
      // ID
      mRoleId: 0
    }
  },
  methods: {
    //  
    async fetchRolesList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('roles')).data
      if (meta.status === 200) {
        var cloneData = _.cloneDeep(data)
        // 
        // cloneData = this.deleteLeafNode(cloneData)
        this.mRolesList = cloneData
        console.log(this.mRolesList)
      } else {
        this.$message.error(meta.msg)
      }
      this.loading = false
    },
    deleteLeafNode(data) {
      console.log(data)
      for (let i = 0; i < data.length; i++) {
        data[i].children = null
      }
      return data
    },
    async fetchPermissionList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('rights/tree')).data
      if (meta.status === 200) {
        this.mPermissionList = data
        // console.log(data)
      } else {
        this.$message.error(meta.msg)
      }
      this.loading = false
    },
    // 
    showPermissionDialog(role) {
      this.mRoleId = role.id
      this.fetchPermissionList()
      this.getIdByLeafNode(role, this.defaultKeys)
      this.permissionDialogVisible = true
    },
    // ID
    getIdByLeafNode(node, arr) {
      if (!node.children) return arr.push(node.id)
      node.children.forEach(item => {
        this.getIdByLeafNode(item, arr)
      })
    },
    // 
    async allotsPermissions() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      var rids = keys.join(',')
      const { meta } = (await this.$http.post(`roles/${this.mRoleId}/rights`, {
        rids
      })).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.$message.success('')
      // role ,,
      // 
      // this.fetchPermissionList()
      this.fetchRolesList()
      this.permissionDialogVisible = false
    },
    // 
    resetDefaultKeys() {
      this.defaultKeys = []
    },
    // idid
    // roles/:roleId/rights/:rightId
    removeTagById(role, permissionId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { meta, data } = (await this.$http.delete(
            `roles/${role.id}/rights/${permissionId}`
          )).data
          if (meta.status === 200) {
            this.$message.success('!')
            // 
            // 
            role.children = data
          }
        })
        .catch(() => {
          this.$message.info('')
        })
    }
  },
  created() {
    this.fetchRolesList()
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px;
}
.borderd-top {
  border-top: 1px solid #f1f1f1;
}
.borderd-bottom {
  border-bottom: 1px solid #f1f1f1;
}
// 
.dispaly-center {
  display: flex;
  align-items: center;
}
</style>
