<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            v-model="queryInfo.query"
            clearable
            v-on:keyup.enter.native="getUsersList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getUsersList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addFormVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="mUserList" style="width: 100%" v-loading="loading" border stripe>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="" prop="mg_state">
          <template slot-scope="socpe">
            <div>
              <el-switch v-model="socpe.row.mg_state" @change="changeUserState(socpe.row)"></el-switch>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="socpe">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(socpe.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUser(socpe.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button
                type="warning"
                icon="el-icon-s-tools"
                size="mini"
                @click="showAllotsRoleDialog(socpe.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 7, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <!-- close -->
    <el-dialog
      title=""
      :close-on-click-modal="false"
      @close="resetAddUserForm"
      :visible.sync="addFormVisible"
      width="30%"
    >
      <el-form :model="userForm" :rules="rules" ref="addUserForm">
        <!-- form-itemprop -->
        <el-form-item prop="username">
          <el-input v-model="userForm.username" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="userForm.password" placeholder="" autocomplete="off" type="password"></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input v-model="userForm.email" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="mobile">
          <el-input v-model="userForm.mobile" placeholder="" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="addFormVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      @close="resetEditUserForm"
      :close-on-click-modal="false"
      :visible.sync="editFormVisible"
      width="30%"
    >
      <el-form :model="userForm" :rules="rules" ref="editFormRef">
        <el-form-item prop="username">
          <el-input v-model="userForm.username" placeholder="" disabled></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input v-model="userForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="mobile">
          <el-input v-model="userForm.mobile" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :close-on-click-modal="false"
      @close="resetSelectedInfo"
      :visible.sync="allotsRoleVisible"
      width="30%"
    >
      <p> {{userInfo.username}}</p>
      <p> {{userInfo.role_name}}</p>
      <p>
        :
        <el-select placeholder="" v-model="selectedId">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id"
          ></el-option>
        </el-select>
      </p>
      <div slot="footer">
        <el-button @click="allotsRoleVisible = false"> </el-button>
        <el-button type="primary" @click="updateRole"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // callback
    var checkMoblie = (rule, value, callback) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) return callback()
      callback(new Error('11'))
    }
    return {
      // 
      loading: false,
      // 
      mUserList: [],
      // 
      total: 0,
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      // 
      addFormVisible: false,
      // 
      editFormVisible: false,
      // 
      allotsRoleVisible: false,
      // 
      userForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      editUserID: 0,
      // 
      rules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 8, message: ' 3  8 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }
        ],
        email: [
          {
            required: true,
            type: 'email',
            message: '',
            trigger: 'blur'
          }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMoblie, trigger: 'blur' }
        ]
      },
      // 
      userInfo: {
        userId: '',
        role_name: '',
        username: ''
      },
      // 
      roleList: [],
      // ID placeholder
      selectedId: ''
    }
  },
  methods: {
    async getUsersList() {
      this.loading = true

      const { data, meta } = (await this.$http.get('users', {
        params: this.queryInfo
      })).data
      this.loading = false

      if (meta.status === 200) {
        this.mUserList = data.users
        this.total = data.total
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 
    handleSizeChange(val) {
      this.queryInfo.pagesize = val
      this.getUsersList()
    },
    //  
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getUsersList()
    },
    // 
    async changeUserState(userInfo) {
      const { meta } = (await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )).data

      if (meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('' + meta.msg)
      }
      this.$message.success('')
    },
    // 
    resetAddUserForm() {
      this.$refs.addUserForm.resetFields()
    },
    // 
    resetEditUserForm() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    addUser() {
      this.$refs['addUserForm'].validate(async valid => {
        if (valid) {
          const { meta } = (await this.$http.post('users', this.userForm)).data
          if (meta.status === 201) {
            this.$message.success(meta.msg)
            this.getUsersList()
          } else {
            this.$message.error(meta.msg)
          }
        } else {
          this.$message.error('')
        }
        // 
        this.addFormVisible = false
      })
    },
    // 
    async showEditDialog(id) {
      this.editFormVisible = true
      const { meta, data } = (await this.$http.get('users/' + id)).data
      if (meta.status === 200) {
        // 
        this.editUserID = id
        this.userForm.username = data.username
        this.userForm.email = data.email
        this.userForm.mobile = data.mobile
      }
    },
    // 
    editUser() {
      this.$refs.editFormRef.validate(async valid => {
        // validfalse 
        if (!valid) return this.$message.error('')
        // 
        const { meta } = (await this.$http.put('users/' + this.editUserID, {
          id: this.editUserID,
          email: this.userForm.username,
          mobile: this.userForm.mobile
        })).data
        if (meta.status === 200) {
          this.getUsersList()
          this.$message.success('')
        } else this.$message.error(meta.msg)
        // 
        this.editFormVisible = false
      })
    },
    //  
    deleteUser(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { meta } = (await this.$http.delete('users/' + id)).data
          if (meta.status === 200) {
            this.$message.success('')
            this.getUsersList()
          } else this.$message.error(meta.msg)
        })
        .catch(() => {
          this.$message.info('')
        })
    },
    //  
    async showAllotsRoleDialog(userinfo) {
      this.userInfo = userinfo

      const { meta, data } = (await this.$http.get('roles')).data
      if (meta.status === 200) {
        this.roleList = data
      } else this.$message.error(meta.msg)
      this.allotsRoleVisible = true
    },
    // 
    async updateRole() {
      const { meta } = (await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedId
      })).data
      if (meta.status === 200) {
        this.$message.success('')
        this.getUsersList()
      } else this.$message.error('')
      this.allotsRoleVisible = false
    },
    // 
    resetSelectedInfo() {
      this.selectedId = ''
      this.userInfo = {}
    }
  },
  mounted() {
    this.getUsersList()
  }
}
</script>

<style lang="less" scoped>
</style>
