<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            clearable
          >
            <el-button
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        ref="orderTable"
        :data="mOrderList"
        style="width: 100%"
        border
        stripe
        v-loading="loading"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column
          prop="order_number"
          label=""
        ></el-table-column>
        <el-table-column
          prop="order_price"
          label=""
          width="95px"
        ></el-table-column>
        <el-table-column
          prop="pay_status"
          label=""
          width="80px"
        >
          <!--  0 1  -->
          <template slot-scope="scope">
            <div>
              <el-tag
                type="success"
                v-if="scope.row.pay_status === '1'"
              ></el-tag>
              <el-tag
                type="error"
                v-else
              ></el-tag>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="is_send"
          label=""
          width="80px"
        ></el-table-column>
        <el-table-column
          prop="create_time"
          label=""
          width="170px"
        >
          <template slot-scope="scope">
            <div>{{scope.row.create_time|dateFormat}}</div>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="130px"
        >
          <template slot-scope="scope">
            <!--  -->
            <el-button
              size="mini"
              type="primary"
              class="el-icon-map-location"
              @click="addressVisible=true"
            ></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="success"
              class="el-icon-truck"
              @click="queryExpressInfoById(scope.row.order_number)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  background  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 25, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="mTotal"
        background
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="resetForm"
    >
      <el-form
        :model="addressForm"
        ref="addressFormRef"
        :rules="addressFormRules"
      >
        <el-form-item
          label="/"
          prop="address"
        >
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }"
          ></el-cascader>
        </el-form-item>
        <el-form-item
          label=""
          prop="detailAddr"
        >
          <el-input
            v-model="addressForm.address2"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addressVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="editAddress"
        > </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="expressVisible"
      width="50%"
    >
      <el-steps
        direction="vertical"
        :active="1"
      >
        <el-step
          :title="item.ftime"
          v-for="(item,i) in expressList"
          :key="i"
          :description="item.context"
        ></el-step>
      </el-steps>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '../../plugins/citydata.js'
export default {
  data() {
    return {
      // 
      loading: false,
      // 
      queryInfo: {
        pagenum: 1,
        pagesize: 10
      },
      // 
      mOrderList: [],
      //   
      mTotal: 0,
      // 
      addressForm: {},
      // 
      addressVisible: false,
      // 
      cityData,
      // 
      addressFormRules: {
        address: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        detailAddr: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      expressList: [],
      // 
      expressVisible: false
    }
  },
  methods: {
    async fetchOrderList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('orders', {
        params: this.queryInfo
      })).data
      this.loading = false
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mOrderList = data.goods
      this.mTotal = data.total
      console.log(data)
    },
    // 
    async queryExpressInfoById(id) {
      console.log(id)

      this.expressVisible = true
      const { data } = (await this.$http.get('/kuaidi/' + '1106975712662')).data
      // if (meta.status !== 200) this.$message.error(meta.msg)
      console.log(data)
      this.expressList = data
    },
    // 
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.fetchOrderList()
    },
    // 
    handleCurrentChange(curPage) {
      this.queryInfo.pagenum = curPage
      this.fetchOrderList()
    },
    // 
    editAddress() {
      this.$refs.addressFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        this.$message.succcess('')
      })
    },
    // 
    resetForm() {
      this.$refs.addressFormRef.resetFields()
    }
  },
  created() {
    this.fetchOrderList()
  }
}
</script>

<style scoped>
.el-cascader {
  width: 100%;
}
</style>
