<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            :model="proName"
            clearable
            v-on:keyup.enter.native="searchPro"
          >
            <el-button slot="append" icon="el-icon-search" @click="searchPro"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddProduct"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        ref="proTable"
        :data="mProductList"
        style="width: 100%"
        border
        stripe
        v-loading="loading"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="80px"></el-table-column>
        <el-table-column prop="add_time" label="" width="170px">
          <template slot-scope="scope">
            <div>{{scope.row.add_time|dateFormat}}</div>
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <!--  -->
            <el-button size="mini" type="primary" class="el-icon-edit"></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="danger"
              class="el-icon-delete"
              @click="deleteProductById(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  background  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 25, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="mTotal"
        background
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      // 
      proName: '',
      // 
      mProductList: [],
      // 
      mTotal: 0,
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      }
    }
  },
  methods: {
    // 
    addProduct() {},
    // 
    searchPro() {},
    // 
    async fetchProductList() {
      this.loading = true
      const { meta, data } = (await this.$http.get('goods', {
        params: this.queryInfo
      })).data
      this.loading = false
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mProductList = data.goods
      this.mTotal = data.total
      console.log(data)
    },
    // 
    handleCurrentChange(curNum) {
      this.queryInfo.pagenum = curNum
      this.fetchProductList()
    },
    // 
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.fetchProductList()
    },
    //  
    deleteProductById(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { meta } = (await this.$http.delete('goods/' + id)).data
          if (meta.status === 200) {
            this.$message.success('')
            this.fetchProductList()
          } else this.$message.error(meta.msg)
        })
        .catch(() => {
          this.$message.info('')
        })
    },
    // 
    goAddProduct() {
      this.$router.push('/goods/add')
    }
  },
  created() {
    this.fetchProductList()
  }
}
</script>
