<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-alert title="" type="warning" show-icon></el-alert>
      <!--  -->
      <el-row class="cascader">
        <el-col>
          <span></span>
          <el-cascader
            v-model="selectedOption"
            :options="mCateList"
            :props="cascaderProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- TAB   -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            size="mini"
            @click="addDialogVisible = true"
          ></el-button>
          <!-- -->
          <el-table :data="dynamicData" style="width: 100%" border stripe v-loading="loading">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <div>
                  <!--  -->
                  <el-tag
                    v-for="(item,i) in scope.row.attr_vals"
                    :key="i"
                    closable
                    @close="handleClose(i,scope.row)"
                  >{{item}}</el-tag>
                  <!--  -->
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  ></el-input>
                  <!--   -->
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                  ></el-button>
                </div>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  class="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  class="el-icon-delete"
                  @click="remove(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isBtnDisable"
            size="mini"
            @click="addDialogVisible = true"
          ></el-button>
          <!--  -->
          <el-table :data="staticData" style="width: 100%" border stripe v-loading="loading">
            <el-table-column type="expand">
              <!--  -  -->
              <template slot-scope="scope">
                <div>
                  <!--  -->
                  <el-tag
                    v-for="(item,i) in scope.row.attr_vals"
                    :key="i"
                    closable
                    @close="handleClose(i,scope.row)"
                  >{{item}}</el-tag>
                  <!--  -->
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  ></el-input>
                  <!--   -->
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                  ></el-button>
                </div>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  class="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  class="el-icon-delete"
                  @click="remove(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="addDialogVisible"
      width="30%"
      @close="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="formRef">
        <el-form-item prop="attr_name" :label="titleText">
          <el-input placeholder="" v-model="form.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParam"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="resetEditForm"
    >
      <el-form :model="editForm" :rules="rules" ref="editFormRef">
        <el-form-item prop="attr_name" :label="titleText">
          <el-input placeholder="" v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParam"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 
      loading: false,
      // 
      mCateList: [],
      //   
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      //   
      selectedOption: [],
      //    many only
      activeName: 'many',
      //   
      dynamicData: [],
      // 
      staticData: [],
      //   
      addDialogVisible: false,
      // 
      rules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      //   
      form: {
        attr_name: '',
        attr_sel: '',
        attr_vals: ''
      },
      //   
      editForm: {
        attr_id: 0,
        attr_name: '',
        attr_sel: '',
        attr_vals: ''
      },
      // 
      editDialogVisible: false
    }
  },
  methods: {
    async fetchCategoriesList() {
      const { meta, data } = (await this.$http.get('categories')).data
      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.mCateList = data
      // console.log(this.mCateList)
    },
    // 
    handleClick(tab, event) {
      this.getParams()
    },
    // 
    handleChange() {
      this.getParams()
    },
    // 
    async getParams() {
      if (this.isBtnDisable) {
        // 
        this.dynamicData = []
        this.staticData = []
        this.selectedOption = []
        return
      }
      /**
      // 
      if (this.dynamicData.length !== 0 && this.staticData.length !== 0) {
        return
      }**/
      if (this.cateID) {
        this.loading = true

        const { meta, data } = (await this.$http.get(
          `categories/${this.cateID}/attributes`,
          {
            params: {
              sel: this.activeName
            }
          }
        )).data

        this.loading = false
        if (meta.status !== 200) return this.$message.error(meta.msg)
        data.forEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          //   
          item.inputVisible = false
          item.inputValue = ''
        })
        // console.log(data)
        if (this.activeName === 'many') {
          this.dynamicData = data
        } else if (this.activeName === 'only') {
          this.staticData = data
        }
      }
    },
    // 
    resetForm() {
      this.$refs.formRef.resetFields()
    },
    // 
    resetEditForm() {
      this.$refs.editFormRef.resetFields()
    },
    //  
    addParam() {
      this.$refs.formRef.validate(async valid => {
        if (!valid) return

        this.form.attr_sel = this.activeName
        const { meta, data } = (await this.$http.post(
          `categories/${this.cateID}/attributes`,
          this.form
        )).data
        console.log(data)

        if (meta.status !== 201) return this.$message.error(meta.msg)
        this.$message.success(`${this.titleText}`)
        this.addDialogVisible = false
        /**
        *
        data.attr_vals = data.attr_vals ? data.attr_vals.split(' ') : []
        //   
        if (this.activeName === 'only') {
          this.staticData.push(data)
          console.log(this.staticData)
        } else {
          this.dynamicData.push(data)
          console.log(this.dynamicData)
        }
        */
        this.getParams()
      })
    },
    // 
    async showEditDialog(attrId) {
      this.editDialogVisible = true
      const { meta, data } = (await this.$http.get(
        `categories/${this.cateID}/attributes/${attrId}`
      )).data

      if (meta.status !== 200) return this.$message.error(meta.msg)
      this.editForm.attr_name = data.attr_name
      this.editForm.attr_id = data.attr_id
    },
    // 
    editParam() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { meta } = (await this.$http.put(
          `categories/${this.cateID}/attributes/${this.editForm.attr_id}`,
          {
            attr_sel: this.activeName,
            attr_name: this.editForm.attr_name
          }
        )).data
        if (meta.status !== 200) this.$message.error(meta.msg)
        this.$message.success(`${this.titleText}`)
        this.editDialogVisible = false
        this.getParams()
      })
    },
    // 
    async remove(attrId) {
      const { meta } = (await this.$http.delete(
        `categories/${this.cateID}/attributes/${attrId}`
      )).data
      if (meta.status !== 200) this.$message.error(meta.msg)
      this.$message.success(`${this.titleText}`)
      this.getParams()
    },
    // 
    showInput(attrItem) {
      attrItem.inputVisible = true
      // 
      // nextTick DOM
      // 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // enter
    // attrItemattrItem
    async handleInputConfirm(attrItem) {
      if (attrItem.inputValue.trim().length !== 0) {
        // 
        const copyAttrItem = _.cloneDeep(attrItem)
        copyAttrItem.attr_vals.push(attrItem.inputValue)
        const res = this.updateAttrVals(copyAttrItem, attrItem)
        console.log(res)
        res.then(resolve => {
          console.log(resolve)
          if (resolve) attrItem.attr_vals.push(attrItem.inputValue)
          // enter
          attrItem.inputVisible = false
          attrItem.inputValue = ''
        })
      }
    },
    // 
    async handleClose(i, attrItem) {
      const copyAttrItem = _.cloneDeep(attrItem)
      copyAttrItem.attr_vals.splice(i, 1)
      const res = this.updateAttrVals(attrItem)
      res.then(resolve => {
        if (resolve) attrItem.attr_vals.splice(i, 1)
      })
    },
    // 
    async updateAttrVals(copyAttrItem, attrItem) {
      // ,
      const { meta } = (await this.$http.put(
        `categories/${this.cateID}/attributes/${copyAttrItem.attr_id}`,
        {
          attr_name: copyAttrItem.attr_name,
          attr_sel: copyAttrItem.attr_sel,
          attr_vals: copyAttrItem.attr_vals.join(' ')
        }
      )).data
      if (meta.status !== 200) {
        this.$message.error(meta.msg)
        return false
      }
      this.$message.success('')
      return true
    }
  },
  created() {
    this.fetchCategoriesList()
  },
  computed: {
    //   ,true, false
    isBtnDisable() {
      if (this.selectedOption.length !== 3) {
        return true
      }
      return false
    },
    // ID
    cateID() {
      if (this.selectedOption.length === 3) {
        return this.selectedOption[2]
      }
      return null
    },
    // -
    titleText() {
      if (this.activeName === 'only') return ''
      return ''
    }
  }
}
</script>

<style scoped>
.cascader {
  margin-top: 15px;
}
/*  */
.el-tag {
  margin: 5px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
