<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-table ref="pTable" :data="mPermissionList" style="width: 100%" border  v-loading="loading">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="authName" label="" column-key="id"></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column prop="level" label="">
          <!--  -->
          <template slot-scope="scope">
            <el-tag
              :type="levelClass[scope.row.level]"
              disable-transitions
            >{{levelClassText[scope.row.level]}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loading: false,
      mPermissionList: [],
      type: 'list',
      levelClass: ['primary', 'success', 'warning'],
      levelClassText: ['', '', '']
    }
  },
  methods: {
    async getPermissionList() {
      // 
      this.loading = true
      // 
      const { meta, data } = (await this.$http.get('rights/' + this.type)).data
      if (meta.status === 200) {
        // console.log(data)
        this.mPermissionList = data
      } else {
        this.$message.error(meta.msg)
      }
      // 
      this.loading = false
    }
  },
  created() {
    this.getPermissionList()
  }
}
</script>

<style lang="less" scoped>
</style>
